@use '../../styles/vars';

.contact {
    height: 70vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 1rem;
    width: 100%;
    
    &__container {
        display: flex;
        justify-content: center;
        align-items: stretch;
        background: rgba(228, 225, 225, 0.603);
        border: 1px solid rgba(255, 255, 255, 0.25);
        box-shadow: 0 15px 25px rgba(0, 0, 0, 0.185);
        border-radius: 20px;
        padding: 20px;
        width: 70%;
        max-width: 2000px;
        gap: 2rem;
        height: 70vh;
        margin-top: 1rem;

        &-left {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            box-shadow: vars.$box-shadow;
            background: vars.$tertiary-color;
            border-radius: 0.5rem;
            width: 40%;
        }

        &-right {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            box-shadow: vars.$box-shadow;
            background: vars.$tertiary-color;
            border-radius: 0.5rem;
            width: 40%;
        }
    }

}

.overlay{
    display: none;
}

.mobile-contact{
    display: none;
    
}

