@media only screen and (max-width: 1200px) {

    .contact__container{
        width: 95%;
    }
    }
    




@media only screen and (max-width: 850px) {
    .contact__container{
        flex-direction: column;
        align-items: center;

        &-left{
            flex-direction: row;
            width: 80%;
        }

        &-right{
            width: 80%;
        }
    }
    
}

@media only screen and (max-width: 600px) {
    .contact{
        height: 80vh;
        

        &__container{
            width: 90%;
            margin-top: 0;
        }


        &__container-left{
            flex-direction: column;
            align-items: center;
        
        }

    }

    .overlay-open{
        position: fixed;
        z-index: 10;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(0,0,0, .4);
        display: block;
    }

    .mobile-contact{
        width: 100%;
    display: flex;flex-direction: column;

    & p{
        text-align: center;
    }
    }

}


@media only screen and (max-width: 400px) {
    .contact__container{
        gap: 0.5rem;
    }
}



@keyframes jump {
    0% {
        transform: translateY(3px);
    }
    100% {
        transform: translateY(5px);
    }
}



@media only screen and (max-height: 550px) {
    .contact{
      height: 130vh;
      margin-bottom: 2rem;
      align-items: flex-start;

      &__container{
         height: 100vh;
      }
    }
   }


   @media only screen and (max-height: 450px) {
    .contact{
      height: 140vh;
      margin-bottom: 2rem;
      align-items: flex-start;

      &__container{
         height: 120vh;
      }
    }
   }



   @media only screen and (max-height: 380px) {
    .contact{
      height: 170vh;
      margin-bottom: 2rem;
      align-items: flex-start;

      &__container{
         height: 150vh;
      }
    }
   }